$(document).ready(function () {

  if ($(window).scrollTop() > 45) {
    $('.header').addClass('sticky')
  }

  $(window).scroll(function () {
    if ($(this).scrollTop() > 45) {
      $('.header').addClass("sticky")
    } else { $('.header').removeClass("sticky") }
  })

  

  //смена активности меню  
  if ($(window).width() > "1200") {
    $(window).on('scroll touchmove', function (event) {
      let curPos = $(this).scrollTop() + 170
      // let wh = $(document).height()
      $(".wrap").each(function () {
        let top = $(this).offset().top
        let bottom = top + $(this).outerHeight()
        if (curPos > top && curPos < bottom) {
          $(".menu__list").find('a').removeClass('is-active')
          $(".menu__list").find(`a[href="index.html#${$(this).attr('id')}"]`).addClass('is-active')         
        } 
      })
    })
  }

  let indent = 0
  $(window).width() < "1200" ? indent = 50 : indent = 40  
  
  //Анимация якорей 
  $('a[href*="#"]')
    // Remove links that don't actually link to anything
    .not('[href="#"]')
    .not('[href="#0"]')
    .click(function (event) {
      // On-page links      
      event.stopPropagation()
      if (
        location.pathname.replace(/^\//, '') == this.pathname.replace(/^\//, '') &&
        location.hostname == this.hostname
      ) {
        // Figure out element to scroll to
        let target = $(this.hash)
        target = target.length ? target : $(`[name=${this.hash.slice(1)}]`)
        // Does a scroll target exist?
        if (target.length) {
          // Only prevent default if animation is actually gonna happen
          event.preventDefault()
          $('html, body').animate({
            scrollTop: target.offset().top - indent
          }, 1000, function () {
            // Callback after animation
            // Must change focus!
            let $target = $(target)
            $target.focus()
            if ($target.is(":focus")) { // Checking if the target was focused
              return false
            }            
          })
        }
      }
      if ( $(window).width() < "1200") {
        $(".header__mobile-item").removeClass("active")    
        $('.header__menu').toggleClass("opened")
        $('.header').toggleClass('menu-opened')     
        $('body').removeClass('load')
  
        // $('.menu__link').removeClass('is-active')
        // $(this).addClass('is-active')

      }

    })
  
  

})



