window.onload = function () {
  function getOffsetSum(elem) {
    var top = 0
    let left = 0

    while (elem) {
      top = top + parseInt(elem.offsetTop)
      left = left + parseInt(elem.offsetLeft)
      elem = elem.offsetParent
    }

    return {
      top: top,
      left: left
    }
  }
  function animateSvg() {
    let zero = 0
    let svg = Array.from(document.getElementsByClassName("network__img"))
    if (zero < svg.length) {
      let scrolled = window.pageYOffset || document.documentElement.scrollTop
      let parent = svg[0].parentElement
      let elemScroll = getOffsetSum(parent).top
      let scrollLimit = 500
      let delayArg = 3000
      if (scrolled + scrollLimit > elemScroll) {
        svg.forEach((el, index) => {
          el.classList.add('is-animating-svg')
          setTimeout(() => {
            el.classList.add('is-animated-svg')
          }, delayArg)
        })
      }
    }
  }
  animateSvg()
  window.onscroll = () => {
    animateSvg()
  }
}




