$(document).ready(function () {

  if ($(window).scrollTop() > "400") {
    $("#parallax-bg-1, #parallax-bg-2").hide()
  }
  
  if ($(window).width() >= 1200) {
    $(window).scroll(function (e) {
      let scrolled = $(window).scrollTop()
      parallaxScroll(scrolled)      
      if (scrolled >= "400") {
        $("#parallax-bg-1, #parallax-bg-2").fadeOut()
        // $(window).off(parallaxScroll(scrolled))
      } else {
        $("#parallax-bg-1, #parallax-bg-2").fadeIn()
      }
      
      let targetBlock = $('#internet').position().top
      if (scrolled > targetBlock) {
        $(".mission__img").addClass("animate")
      }
    
    })
  }

   
  function parallaxScroll(scrolled) {  
    $('#parallax-bg-1').css('top', `${(0-(scrolled*.6))}px`)
    $('#parallax-bg-2').css('top', `${(0-(scrolled*.2))}px`)    
  }
    
 
})



